<template>
    <div class="playlist">
        <myScroll class="scroll">
            <div>
                <div class="header">
                    <div @click="back" class="logo">
                        <i class="iconfont icon-back"></i>
                    </div>
                    <div class="tab" :style="'background:url('+playlist.coverImgUrl+')'">
                        <div>
                            <img width="126px" height="126px" :src="playlist.coverImgUrl" alt="">
                        </div>
                        <div class="right">
                            <span>{{playlist.name}}</span>
                            <div class="author">
                                <!-- <img :src="playlist.creator.avatarUrl" alt=""> -->
                                <span class="author-name"></span>
                            </div>
                        </div>
                    </div>
                    <div class="tags">
                        标签:
                        <span class="tag" v-for="(item,i) in playlist.tags" :key="i">
                            {{item}}
                        </span>
                    </div>
                </div>
                <div class="content">
                    <span>简介：{{playlist.description}}</span>
                </div>
                <p>歌曲列表</p>        
                <ul class="list">
                    <li @click="play(item.id)" class="item" v-for="(item,i) in playlist.tracks" :key="i">
                        <!-- <span>{{item.name}}</span>
                        <span>{{item.ar[0].name}}</span>
                        <span>{{item.al.name}}</span> -->
                        <div>
                            <div class="name">
                                <span>{{item.name.slice(0,15)}}</span>
                            </div>
                            <div class="author">
                                <span>{{item.al.name}}</span>&nbsp;-
                                <span>{{item.ar[0].name}}</span>
                            </div>
                            <i class="iconfont icon-iconset0481"></i>
                        </div>
                    </li>
                </ul>
            </div>
        </myScroll>
    </div>
</template>

<script>
    import myScroll from '@/base/scroll/scroll';
    import { getPlaylist } from '@/api/recommend';
    export default {
        data () {
            return {
                playlist: []
            }
        },
        methods: {
            play(id){
                this.$router.push({path: 'song',query:{id:id}})
            },
            back(){
                this.$router.back()
            },
        },
        computed: {
            id(){
                return this.$route.query.id
            }
        },
        created(){            
            console.log(this.$route.query.id);
            
            getPlaylist(this.id).then(res => {
                this.playlist = res
                console.log(this.playlist);
                                
            })
        },
        components: {
            myScroll
        }
    }
</script>
<style lang="less" scoped>
    @import "../../common/style/variable.less";
    .playlist {
        width: 100%;
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        .scroll{
            height: 100%;
            overflow: hidden;
            .header {
                position: relative;
                .logo {
                    position: absolute;
                    left: 10px;
                    top: 10px;
                    .iconfont {
                        font-size: @font-size-big;
                    }
                    z-index: 10;
                }
                .tab {
                    position: relative;
                    background-size: cover;
                    &::before {
                        content: "";
                        position: absolute;
                        filter:blur(5px);
                        z-index: -1;
                        width: 100%;
                        height: 100%;
                        top: 0px;
                        left: 0px;
                        background-size: cover;
                        overflow:hidden;
                    }
                    padding: 30px 10px;
                    display: flex;
                    justify-content: space-around;
                    .right {
                        width: 180px;
                        color: @color-text-l;
                        font-size: @font-size-xxxl;
                    }
                    
                }
                .tags {
                    height: 30px;
                    padding-left: 10px;
                    padding-top: 10px;
                    .tag {
                        display: inline-block;
                        text-align: center;
                        height: 20px;
                        width: 40px;
                        line-height: 20px;
                        border: 1px solid rgb(219, 209, 209);
                        border-radius: 20px;
                        margin-left: 10px;
                        font-size: @font-size-l;
                        color: @color-text-g;
                    }
                }
            }
            .content {
                padding: 10px;
                font-size: @font-size-l;
                color: @color-text-g;
                line-height: 18px;
            }
            P {
                height: 23px;
                padding-left: 10px;
                font-size: @font-size-l;
                color: @color-text-g;
                background: rgb(231, 225, 225);
                line-height: 23px;
            }
            ul {
                display: flex;
                flex-wrap: wrap;
                li {
                    width: 100%;
                    height: 40px;
                    flex: 1 1 auto;
                    padding-left: 10px;
                    padding-top: 10px;
                    position: relative;
                    .name {
                        span {
                            font-size: @font-size-xxl;
                        }
                    }
                    .author {
                        padding-top: 5px;         
                        span {
                            color: @color-text-g;
                            font-size: @font-size-m;
                        }
                    }
                    .iconfont {
                        position: absolute;
                        right: 10px;
                        top: 5px;
                        font-size: @font-size-big;
                    }
                }
            }
        }
    }
</style>