import axios from 'axios'

axios.defaults.baseURL = 'http://summerboy.cn:3000/'

// 登陆

// 获取banner

export const getBanner = () => {
    return  axios.get('/banner').then(res => {
        return Promise.resolve(res.data.banners)
    })
}

// 获取推荐歌单

export const getRecommendList = () => {
    return axios.get('/personalized').then(res => {
        return Promise.resolve(res.data.result)
    })
} 
// 获取歌单详情

export const getPlaylist = (id) => {
    return axios.get(`/playlist/detail?id=${id}`).then(res => {
        return Promise.resolve(res.data.playlist)
    })
} 


// 获取最新音乐
export const getNewMusic = () => {
    return axios.get('/personalized/newsong').then(res => {
        return Promise.resolve(res.data.result)
    })
}